/**
 * 
 * @param props - 
 * @param props.to - 
 * @param props.id - 
 * @param props.src - 
 * @returns  `src`  `title`
 */
export const getVideoConfig = (props: VideoProps) => {
  /**
   *  `to`  `id`
   * @example
   * getVideoConfig({ to: 'bilibili', id: '12345' });
   * //  { src: 'https://player.bilibili.com/player.html?aid=12345', title: 'Bilibili video player' }
   */
  if (props.to && props.id) {
    return video[props.to]
  }

  /**
   *  `id`  YouTube 
   * @example
   * getVideoConfig({ id: 'abcd1234' });
   * //  { src: 'https://www.youtube-nocookie.com/embed/abcd1234', title: 'YouTube video player' }
   */
  if (props.id) {
    return video.youtube
  }

  /**
   *  `to`  `id` `src`
   *  `src` 
   * @example
   * getVideoConfig({ src: 'https://example.com/custom-video.mp4' });
   * //  { src: 'https://example.com/custom-video.mp4', title: 'Custom video player' }
   */
  return {
    src: props.src || '',
    title: 'Custom video player'
  }
}
